<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
              :src="require('../../assets/icons/home-01.png')"
              contain
              height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">Mi abogado LegalHelp</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Tipo de consulta</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title">Consulta un abogado</div>
          <p class="text-center mb-5">Nuestros abogados atiende en tres modalidades</p>

          <div v-for="(item, i) in services" :key="i" class="call-to-action" @click="goTo(item)">
            <div class="icon">
              <img :src="require('../../assets/icons/'+item.icon)">
            </div>
            <div class="text">{{ item.name + ' S/ ' + item.price }}</div>
          </div>

          <div class="text-center" style="margin: 2rem 0">
            <v-btn @click="goToRequests()" color="primary" width="240">
              Todas mis consultas
              <v-img :src="require('../../assets/icons/lawyer-04.svg')" height="20" contain/>
            </v-btn>
          </div>

          <div style="width: 80%; margin: 0 auto">
            <v-img
                :src="require('../../assets/conference.svg')"
            />
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  name: 'MyLawyerType',
  data: () => ({}),
  computed: {
    ...mapGetters(['user', 'services'])
  },
  methods: {
    ...mapActions(['selectService']),
    goTo(item) {
      this.selectService(item)
      this.$router.push({name: 'MyLawyerPaymentType', params: {id: item.id}})
    },
    goToRequests() {
      this.$router.push({name: 'RequestIndex'})
    }
  }
}
</script>
